.container{
    border: 1px solid black;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
@media (max-width: 746px) {
    .container {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}